html {
  box-sizing: border-box;
  font-size: 62.5%;
  width: 100vw;
}

*, *:before, *:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  background-color: hsl(0deg, 0%, 98%);
  font-family: "Epilogue", sans-serif;
  width: 100vw;
}

.container {
  margin: 0 auto;
  max-width: 150rem;
  width: 90%;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
}

h1 {
  margin: 0;
}

.active {
  display: none;
}

.low-brightness {
  filter: brightness(30%);
}

.footer {
  color: hsl(0deg, 0%, 41%);
  padding-block: 1.95rem;
  text-align: center;
}
@media screen and (min-width: 375px) {
  .footer {
    padding-block: 1.4rem;
  }
}
@media screen and (min-width: 1440px) {
  .footer {
    padding: 2rem 0 0;
  }
}
@media screen and (min-width: 500px) {
  .footer__attribution {
    font-size: 1.2rem;
  }
}
@media screen and (min-width: 1440px) {
  .footer__attribution {
    font-size: 1.6rem;
  }
}
.footer__link-frontendmentor, .footer__link-profile {
  font-weight: 700;
}

.header {
  padding-block: 2rem;
  width: 100vw;
}
@media screen and (min-width: 1440px) {
  .header {
    padding-block: 2.5rem;
  }
}
.header .navbar {
  display: flex;
  justify-content: space-between;
}
@media screen and (min-width: 1440px) {
  .header .navbar {
    align-items: center;
  }
}
.header .navbar__menu-collapsed {
  background-color: white;
  height: 100vh;
  padding: 2rem;
  position: fixed;
  right: 0;
  top: 0;
  transform: translateX(100%);
  transition: transform 0.3s ease-in-out;
  width: 65%;
  z-index: 10;
}
@media screen and (min-width: 500px) {
  .header .navbar__menu-collapsed {
    width: 35%;
  }
}
@media screen and (min-width: 1440px) {
  .header .navbar__menu-collapsed {
    height: 2.7rem;
    left: -35rem;
    padding: 0;
    top: unset;
  }
}

@media screen and (min-width: 1440px) {
  .navbar__menu {
    display: none;
  }
}

.navbar__menu-collapsed[data-visible=true] {
  transform: translateX(0%);
}

.navbar__flex {
  display: flex;
  justify-content: flex-end;
}
@media screen and (min-width: 1440px) {
  .navbar__flex {
    display: none;
  }
}

.navbar__lists {
  margin-top: 3rem;
}
@media screen and (min-width: 1440px) {
  .navbar__lists {
    column-gap: 2rem;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top: 0;
  }
}
.navbar__lists .navbar__list-features,
.navbar__lists .navbar__list-company,
.navbar__lists .navbar__list {
  line-height: 2.7rem;
  list-style: none;
}
@media screen and (min-width: 1440px) {
  .navbar__lists .navbar__list-features,
.navbar__lists .navbar__list-company,
.navbar__lists .navbar__list {
    height: 2.7rem;
    padding-block: 0;
  }
}
.navbar__lists .navbar__button {
  align-items: center;
  background-color: white;
  border: none;
  color: hsl(0deg, 0%, 41%);
  display: flex;
  font-size: 1.6rem;
  padding: 1rem 0;
}
@media screen and (min-width: 1440px) {
  .navbar__lists .navbar__button:hover {
    color: hsl(0deg, 0%, 8%);
    cursor: pointer;
  }
}
@media screen and (min-width: 1440px) {
  .navbar__lists .navbar__button {
    line-height: 2.7rem;
    margin-inline: auto;
    padding: 0;
  }
  .navbar__lists .navbar__button#company {
    margin-inline: 0;
  }
}

.navbar__sub-lists-dropdown-features,
.navbar__sub-lists-dropdown-company {
  margin-top: 1.5rem;
}

@media screen and (min-width: 1440px) {
  .navbar__sub-lists-dropdown-features {
    transform: translateX(-3.5rem);
    width: 17rem;
  }
}

@media screen and (min-width: 1440px) {
  .navbar__sub-lists-dropdown-company {
    width: 14rem;
  }
}

@media screen and (min-width: 1440px) {
  .navbar__sub-lists-features {
    margin-top: -20rem;
  }
}

.navbar__list {
  padding: 1rem 0;
}
@media screen and (min-width: 1440px) {
  .navbar__list {
    display: flex;
    justify-content: center;
    padding: 0 auto;
  }
}

.navbar__link {
  color: hsl(0deg, 0%, 41%);
  font-size: 1.6rem;
}
@media screen and (min-width: 1440px) {
  .navbar__link:hover {
    color: hsl(0deg, 0%, 8%);
    cursor: pointer;
  }
}

.navbar__arrow-down-img {
  width: 1rem;
}

.navbar__arrow-downF,
.navbar__arrow-upF,
.navbar__arrow-downC,
.navbar__arrow-upC {
  padding-left: 2rem;
  width: 3rem;
}

.navbar__sub-lists-features,
.navbar__sub-lists-company {
  height: 0;
  margin-top: 1rem;
  opacity: 0;
  padding-left: 2.5rem;
  transform: translateY(-10px);
  transition: all 0.5s ease-in-out;
}
@media screen and (min-width: 1440px) {
  .navbar__sub-lists-features,
.navbar__sub-lists-company {
    border-radius: 1rem;
    box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.3);
    padding: 1.5rem 2rem;
    width: 100%;
  }
}
.navbar__sub-lists-features[data-visible=true],
.navbar__sub-lists-company[data-visible=true] {
  height: 100%;
  opacity: 1;
  transform: translateY(0%);
}
.navbar__sub-lists-features .navbar__sub-list,
.navbar__sub-lists-company .navbar__sub-list {
  align-items: center;
  column-gap: 1.5rem;
  display: flex;
  list-style: none;
  margin-bottom: 2.5rem;
}
@media screen and (min-width: 1440px) {
  .navbar__sub-lists-features .navbar__sub-list,
.navbar__sub-lists-company .navbar__sub-list {
    margin-bottom: 1rem;
  }
}
.navbar__sub-lists-features .navbar__sub-list .navbar__sub-link,
.navbar__sub-lists-company .navbar__sub-list .navbar__sub-link {
  color: hsl(0deg, 0%, 41%);
  font-size: 1.4rem;
}

.navbar__sub-list-figure {
  height: 1.6rem;
  width: 1.6rem;
}
.navbar__sub-list-figure .navbar__sub-list-img {
  height: 100%;
  width: 100%;
}

.navbar__login-register {
  display: none;
}
@media screen and (min-width: 1440px) {
  .navbar__login-register {
    align-items: center;
    column-gap: 3rem;
    display: flex;
  }
}
.navbar__login-register .navbar__login,
.navbar__login-register .navbar__register {
  color: hsl(0deg, 0%, 41%);
  font-size: 1.6rem;
}
.navbar__login-register .navbar__login:hover {
  color: hsl(0deg, 0%, 8%);
}

.navbar__register {
  border: 0.2rem hsl(0deg, 0%, 41%) solid;
  border-radius: 1rem;
  padding: 1rem 2rem;
  transition: all ease 0.3s;
}
.navbar__register:hover {
  border-color: hsl(0deg, 0%, 8%);
  color: hsl(0deg, 0%, 8%);
}

.main {
  display: grid;
  grid-template-rows: 29rem auto;
  width: 100vw;
}
@media screen and (min-width: 500px) {
  .main {
    grid-template-rows: repeat(2, auto);
  }
}
@media screen and (min-width: 1440px) {
  .main {
    column-gap: 13rem;
    grid-template-columns: 60rem auto;
    grid-template-rows: 60rem;
    padding: 5rem 16rem 1.5rem;
  }
}
.main .hero-mobile {
  width: 100%;
}
@media screen and (min-width: 500px) {
  .main .hero-mobile {
    margin: 0 auto;
    max-width: 70rem;
  }
}
@media screen and (min-width: 1440px) {
  .main .hero-mobile {
    display: none;
  }
}
.main .hero-mobile__img {
  height: 100%;
  width: 100%;
}

.hero-desktop {
  display: none;
}
@media screen and (min-width: 1440px) {
  .hero-desktop {
    display: block;
  }
}
@media screen and (min-width: 1440px) {
  .hero-desktop__img {
    height: 100%;
  }
}

.content {
  padding-block: 2rem;
}
@media screen and (min-width: 375px) {
  .content {
    padding-block: 3rem;
  }
}
@media screen and (min-width: 1440px) {
  .content {
    order: -1;
    padding-block: 0;
    padding-top: 11rem;
  }
}
.content .flex {
  display: flex;
  flex-direction: column;
  row-gap: 4rem;
}
@media screen and (min-width: 1440px) {
  .content .flex {
    height: 100%;
    justify-content: space-between;
    row-gap: 6rem;
    width: 100%;
  }
}

.info {
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
}
@media screen and (min-width: 1440px) {
  .info {
    row-gap: 3rem;
  }
}
.info__title {
  font-size: 3rem;
  text-align: center;
}
@media screen and (min-width: 375px) {
  .info__title {
    font-size: 3.5rem;
  }
}
@media screen and (min-width: 1440px) {
  .info__title {
    font-size: 9rem;
    line-height: 1;
    text-align: left;
  }
}
.info__paragraph {
  color: hsl(0deg, 0%, 41%);
  font-size: 1.6rem;
  line-height: 1.5;
  text-align: center;
}
@media screen and (min-width: 1440px) {
  .info__paragraph {
    font-size: 2.1rem;
    text-align: left;
    width: 50rem;
  }
}

.info__cta {
  background-color: hsl(0deg, 0%, 8%);
  border-radius: 1rem;
  color: hsl(0deg, 0%, 98%);
  font-size: 1.6rem;
  margin: 0 auto;
  padding: 1rem 2rem;
}
@media screen and (min-width: 1440px) {
  .info__cta {
    border: 0.1rem hsl(0deg, 0%, 41%) solid;
    border-radius: 1.5rem;
    margin: 0;
    padding: 2rem;
    text-align: center;
    transition: all ease-in-out 0.4s;
    width: 19rem;
  }
  .info__cta:hover {
    background-color: hsl(0deg, 0%, 98%);
    color: hsl(0deg, 0%, 8%);
    font-weight: 700;
  }
}

.clients {
  align-items: center;
  column-gap: 4rem;
  display: grid;
  grid-template-columns: 7rem repeat(3, auto);
}
@media screen and (min-width: 500px) {
  .clients {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media screen and (min-width: 1440px) {
  .clients {
    width: 55rem;
  }
}
.clients__img {
  margin-inline: auto;
}